import React, { useState, useRef } from "react";
import { CalendarCheck } from "lucide-react";

const ContactComponent = () => {
  const [selectedService, setSelectedService] = useState("Choose Activity");
  const [showServiceDropdown, setShowServiceDropdown] = useState(false);
  const [customerName, setCustomerName] = useState("");
  const [preferredDate, setPreferredDate] = useState("");
  const [preferredTime, setPreferredTime] = useState("Choose Time");
  const [showTimeDropdown, setShowTimeDropdown] = useState(false);
  const [timeSlots, setTimeSlots] = useState<string[]>([]);
  const serviceRef = useRef("Choose Activity");

  const services = [
    "VitalMan Advanced Blood Test",
    "VitalWoman Advanced Blood Test",
    "Tired All the Time",
    "General Health Screen",
    "Thyroid Panel (TSH, Free T4, fT3)",
    "Aches & Pains",
    "Mounjaro Monitoring",
    "Abdominal Pain",
    "Weight Management Test",
    "Stress Less",
    "Prostate (PSA)",
    "Wedding venue",
    "Billiards",
    "Gym Access",
    "Swimming Pool",
    "Tennis Court",
    "Squash Court",
    "Badminton Court",
  ];

  const isWeekend = (d: Date) => {
    const day = d.getDay();
    return day === 0 || day === 6;
  };

  const generateSlots = (startHour: number, endHour: number) => {
    const slots: string[] = [];
    for (let h = startHour; h <= endHour; h++) {
      for (const m of [0, 30]) {
        if (h === endHour && m === 30) continue;
        slots.push(formatTime(h, m));
      }
    }
    return slots;
  };

  const formatTime = (hour24: number, minute: number) => {
    const h12 = ((hour24 + 11) % 12) + 1;
    const ampm = hour24 >= 12 ? "PM" : "AM";
    const mm = minute.toString().padStart(2, "0");
    return `${h12}:${mm} ${ampm}`;
  };

  const getSlotsForDate = (dateStr: string) => {
    if (!dateStr) return [];
    const d = new Date(dateStr + "T00:00:00");
    return isWeekend(d) ? generateSlots(9, 21) : generateSlots(16, 21);
  };

  const handleSelectService = (service: string) => {
    setSelectedService(service);
    serviceRef.current = service;
    setShowServiceDropdown(false);
  };

  const handleSelectTime = (time: string) => {
    setPreferredTime(time);
    setShowTimeDropdown(false);
  };

  const onDateChange: React.ChangeEventHandler<HTMLInputElement> = (e) => {
    const nextDate = e.target.value;
    setPreferredDate(nextDate);
    setPreferredTime("Choose Time");
    setShowTimeDropdown(false);
    setTimeSlots(getSlotsForDate(nextDate));
  };

  const formatDate = (dateString: string) => {
    if (!dateString) return "";
    const date = new Date(dateString);
    return date.toLocaleDateString("en-GB", {
      weekday: "long",
      year: "numeric",
      month: "long",
      day: "numeric",
    });
  };

  const handleWhatsAppClick = () => {
    const currentService = serviceRef.current;
    const phoneNumber = "447404730923";

    let message = "Hi, I'd like to book an appointment for your Activity.";

    if (
      currentService !== "Choose Activity" ||
      customerName ||
      preferredDate ||
      preferredTime !== "Choose Time"
    ) {
      message = "Hi, I'd like to book an appointment with the following details:\n\n";

      if (customerName) message += `Name: ${customerName}\n`;
      if (currentService !== "Choose Activity")
        message += `Service: ${currentService}\n`;
      if (preferredDate) message += `Preferred Date: ${formatDate(preferredDate)}\n`;
      if (preferredTime !== "Choose Time")
        message += `Preferred Time: ${preferredTime}\n`;

      message += "\nPlease confirm availability. Thank you!";
    }

    const whatsappUrl = `https://wa.me/${phoneNumber}?text=${encodeURIComponent(message)}`;
    window.open(whatsappUrl, "_blank");
  };

  const today = new Date().toISOString().split("T")[0];

  return (
    <div className="w-full max-w-5xl mx-auto mt-2 px-2 2xl:px-4 font-elmessiri z-20">
      {/* Mobile Layout */}
      <div className="md:hidden">
        <div className="bg-[#DC9A09] rounded-lg p-3 shadow-2xl">
          <div className="space-y-3 text-[#FAEBCD] text-sm">
            {/* Name Input */}
            <div>
              <label className="block text-[#FAEBCD] font-semibold mb-1.5 text-sm">
                Your Name
              </label>
              <input
                type="text"
                value={customerName}
                onChange={(e) => setCustomerName(e.target.value)}
                placeholder="Enter your name"
                className="w-full px-3 py-2 rounded-lg bg-[#FAEBCD]/10 border border-[#FAEBCD]/20 text-[#FAEBCD] focus:outline-none focus:ring-2 focus:ring-[#FAEBCD]/30 focus:border-transparent text-sm"
              />
            </div>

            {/* Service */}
            <div className="relative">
              <label className="block text-[#FAEBCD] font-semibold mb-1.5 text-sm">
                Choose Activity
              </label>
              <div className="relative">
                <button
                  onClick={() => setShowServiceDropdown(!showServiceDropdown)}
                  className="w-full text-left px-3 py-2 rounded-lg bg-[#FAEBCD]/10 border border-[#FAEBCD]/20 text-[#FAEBCD] hover:text-[#FAEBCD] transition-colors duration-200 flex items-center justify-between text-sm"
                >
                  <span className="text-[#FAEBCD]">{selectedService}</span>
                  <svg
                    className={`w-4 h-4 transition-transform duration-200 ${
                      showServiceDropdown ? "transform rotate-180" : ""
                    }`}
                    fill="none"
                    stroke="currentColor"
                    viewBox="0 0 24 24"
                  >
                    <path
                      strokeLinecap="round"
                      strokeLinejoin="round"
                      strokeWidth={2}
                      d="M19 9l-7 7-7-7"
                    />
                  </svg>
                </button>
                {showServiceDropdown && (
                  <div className="absolute top-full left-0 right-0 mt-1 bg-[#FAEBCD] rounded-lg shadow-lg z-[100] border max-h-48 overflow-y-auto">
                    {services.map((service, index) => (
                      <button
                        key={index}
                        onClick={() => handleSelectService(service)}
                        className="w-full text-left px-4 py-2 text-gray-700 hover:bg-gray-50 first:rounded-t-lg last:rounded-b-lg transition-colors duration-150 text-sm"
                      >
                        {service}
                      </button>
                    ))}
                  </div>
                )}
              </div>
            </div>

            {/* Date and Time */}
            <div className="grid grid-cols-2 gap-3">
              <div>
                <label className="block text-[#FAEBCD] font-semibold mb-1.5 text-sm">
                  Preferred Date
                </label>
                <input
                  type="date"
                  value={preferredDate}
                  onChange={onDateChange}
                  min={today}
                  className="w-full px-3 py-2 rounded-lg bg-[#FAEBCD]/10 border border-[#FAEBCD]/20 text-[#FAEBCD] focus:outline-none focus:ring-2 focus:ring-[#FAEBCD]/30 focus:border-transparent scheme-dark text-sm"
                />
              </div>

              <div className="relative">
                <label className="block text-[#FAEBCD] font-semibold mb-1.5 text-sm">
                  Preferred Time
                </label>
                <div className="relative">
                  <button
                    onClick={() => preferredDate && setShowTimeDropdown(!showTimeDropdown)}
                    disabled={!preferredDate || timeSlots.length === 0}
                    className={`w-full text-left px-3 py-2 rounded-lg border flex items-center justify-between transition-colors duration-200 text-sm
                      ${
                        !preferredDate || timeSlots.length === 0
                          ? "bg-[#FAEBCD]/5 border-[#FAEBCD]/10 text-[#FAEBCD] cursor-not-allowed"
                          : "bg-[#FAEBCD]/10 border-[#FAEBCD]/20 text-[#FAEBCD] hover:text-[#FAEBCD]"
                      }`}
                  >
                    <span className="text-[#FAEBCD]">{preferredTime}</span>
                    <svg
                      className={`w-4 h-4 transition-transform duration-200 ${
                        showTimeDropdown ? "transform rotate-180" : ""
                      }`}
                      fill="none"
                      stroke="currentColor"
                      viewBox="0 0 24 24"
                    >
                      <path
                        strokeLinecap="round"
                        strokeLinejoin="round"
                        strokeWidth={2}
                        d="M19 9l-7 7-7-7"
                      />
                    </svg>
                  </button>
                  {showTimeDropdown && (
                    <div className="absolute top-full left-0 right-0 mt-1 bg-[#FAEBCD] rounded-lg shadow-lg z-[100] border max-h-48 overflow-y-auto">
                      {timeSlots.map((time, index) => (
                        <button
                          key={index}
                          onClick={() => handleSelectTime(time)}
                          className="w-full text-left px-4 py-2 text-gray-700 hover:bg-gray-50 first:rounded-t-lg last:rounded-b-lg transition-colors duration-150 text-sm"
                        >
                          {time}
                        </button>
                      ))}
                      {timeSlots.length === 0 && (
                        <div className="px-4 py-3 text-sm text-gray-600">
                          No times available
                        </div>
                      )}
                    </div>
                  )}
                </div>
              </div>
            </div>

            {/* WhatsApp Button */}
            <div className="pt-2">
              <button
                onClick={handleWhatsAppClick}
                className="w-full bg-[#0077b6] hover:bg-[#0077b6] text-[#FAEBCD] font-semibold py-3 px-6 rounded-xl transition-colors duration-200 shadow-md flex items-center justify-center text-sm"
              >
                <svg className="w-5 h-5 mr-2" fill="currentColor" viewBox="0 0 24 24">
                  <path d="M17.472 14.382c-.297-.149-1.758-.867-2.03-.967-.273-.099-.471-.148-.67.15-.197.297-.767.966-.94 1.164-.173.199-.347.223-.644.075-.297-.15-1.255-.463-2.39-1.475-.883-.788-1.48-1.761-1.653-2.059-.173-.297-.018-.458.13-.606.134-.133.298-.347.446-.52.149-.174.198-.298.298-.497.099-.198.05-.371-.025-.52-.075-.149-.669-1.612-.916-2.207-.242-.579-.487-.5-.669-.51-.173-.008-.371-.01-.57-.01-.198 0-.52.074-.792.372-.272.297-1.04 1.016-1.04 2.479 0 1.462 1.065 2.875 1.213 3.074.149.198 2.096 3.2 5.077 4.487.709.306 1.262.489 1.694.625.712.227 1.36.195 1.871.118.571-.085 1.758-.719 2.006-1.413.248-.694.248-1.289.173-1.413-.074-.124-.272-.198-.57-.347" />
                </svg>
                Contact on WhatsApp
              </button>
            </div>
          </div>
        </div>
      </div>

      {/* Desktop Layout */}
      <div className="hidden md:block">
        <div className="bg-[#DC9A09] rounded-xl p-2 lg:p-3 xl:p-4 2xl:p-5 shadow-2xl">
          {/* Title */}
          <div className="text-center mb-2 lg:mb-3 xl:mb-4">
            <h2 className="text-[#FAEBCD] font-bold text-xs md:text-sm lg:text-base mb-1">
              Book Your Appointment
            </h2>
          </div>

          {/* Main Grid */}
          <div className="grid grid-cols-3 gap-2 lg:gap-3 xl:gap-4">
            {/* Left Column - Form Fields */}
            <div className="col-span-2 space-y-2 lg:space-y-3 xl:space-y-4">
              {/* First Row: Name and Service */}
              <div className="grid grid-cols-2 gap-2 lg:gap-3 xl:gap-4">
                {/* Name Input */}
                <div>
                  <label className="block text-[#FAEBCD] font-semibold mb-1 text-xs md:text-sm lg:text-base">
                    Your Name
                  </label>
                  <input
                    type="text"
                    value={customerName}
                    onChange={(e) => setCustomerName(e.target.value)}
                    placeholder="Enter your name"
                    className="w-full px-2 lg:px-2.5 py-1.5 lg:py-2 xl:py-2.5 rounded-lg bg-[#FAEBCD]/10 border border-[#FAEBCD]/20 text-[#FAEBCD] placeholder-gray-300 focus:outline-none focus:ring-2 focus:ring-[#FAEBCD]/30 focus:border-transparent text-xs md:text-sm lg:text-base"
                  />
                </div>

                {/* Service Selection */}
                <div className="relative">
                  <label className="block text-[#FAEBCD] font-semibold mb-1 text-xs md:text-sm lg:text-base">
                    Choose Activity
                  </label>
                  <div className="relative">
                    <button
                      onClick={() => setShowServiceDropdown(!showServiceDropdown)}
                      className="w-full text-left px-2 lg:px-2.5 py-1.5 lg:py-2 xl:py-2.5 rounded-lg bg-[#FAEBCD]/10 border border-[#FAEBCD]/20 text-[#FAEBCD] hover:text-[#FAEBCD] transition-colors duration-200 flex items-center justify-between text-xs md:text-sm lg:text-base"
                    >
                      <span className="text-[#FAEBCD]">{selectedService}</span>
                      <svg
                        className={`w-3 h-3 lg:w-4 lg:h-4 transition-transform duration-200 text-[#FAEBCD] ${
                          showServiceDropdown ? "transform rotate-180" : ""
                        }`}
                        fill="none"
                        stroke="currentColor"
                        viewBox="0 0 24 24"
                      >
                        <path
                          strokeLinecap="round"
                          strokeLinejoin="round"
                          strokeWidth={2}
                          d="M19 9l-7 7-7-7"
                        />
                      </svg>
                    </button>
                    {showServiceDropdown && (
                      <div className="absolute top-full left-0 right-0 mt-1 bg-[#FAEBCD] rounded-lg shadow-lg z-[100] border max-h-32 lg:max-h-40 overflow-y-auto">
                        {services.map((service, index) => (
                          <button
                            key={index}
                            onClick={() => handleSelectService(service)}
                            className="w-full text-left px-2.5 lg:px-3 py-1.5 lg:py-2 text-gray-700 hover:bg-gray-50 first:rounded-t-lg last:rounded-b-lg transition-colors duration-150 text-xs md:text-sm lg:text-base"
                          >
                            {service}
                          </button>
                        ))}
                      </div>
                    )}
                  </div>
                </div>
              </div>

              {/* Second Row: Date and Time */}
              <div className="grid grid-cols-2 gap-2 lg:gap-3 xl:gap-4">
                {/* Date Input */}
                <div>
                  <label className="block text-[#FAEBCD] font-semibold mb-1 text-xs md:text-sm lg:text-base">
                    Preferred Date
                  </label>
                  <input
                    type="date"
                    value={preferredDate}
                    onChange={onDateChange}
                    min={today}
                    className="w-full px-2 lg:px-2.5 py-1.5 lg:py-2 xl:py-2.5 rounded-lg bg-[#FAEBCD]/10 border border-[#FAEBCD]/20 text-[#FAEBCD] focus:outline-none focus:ring-2 focus:ring-[#FAEBCD]/30 focus:border-transparent scheme-dark text-xs md:text-sm lg:text-base"
                  />
                </div>

                {/* Time Selection */}
                <div className="relative">
                  <label className="block text-[#FAEBCD] font-semibold mb-1 text-xs md:text-sm lg:text-base">
                    Preferred Time
                  </label>
                  <div className="relative">
                    <button
                      onClick={() => preferredDate && setShowTimeDropdown(!showTimeDropdown)}
                      disabled={!preferredDate || timeSlots.length === 0}
                      className={`w-full text-left px-2 lg:px-2.5 py-1.5 lg:py-2 xl:py-2.5 rounded-lg border flex items-center justify-between transition-colors duration-200 text-xs md:text-sm lg:text-base
                        ${
                          !preferredDate || timeSlots.length === 0
                            ? "bg-[#FAEBCD]/5 border-[#FAEBCD]/10 text-[#FAEBCD] cursor-not-allowed"
                            : "bg-[#FAEBCD]/10 border-[#FAEBCD]/20 text-[#FAEBCD] hover:text-[#FAEBCD]"
                        }`}
                    >
                      <span className="text-[#FAEBCD]">{preferredTime}</span>
                      <svg
                        className={`w-3 h-3 lg:w-4 lg:h-4 transition-transform duration-200 ${
                          showTimeDropdown ? "rotate-180" : ""
                        }`}
                        fill="none"
                        stroke="currentColor"
                        viewBox="0 0 24 24"
                      >
                        <path
                          strokeLinecap="round"
                          strokeLinejoin="round"
                          strokeWidth={2}
                          d="M19 9l-7 7-7-7"
                        />
                      </svg>
                    </button>
                    {showTimeDropdown && (
                      <div className="absolute top-full left-0 right-0 mt-1 bg-[#FAEBCD] rounded-lg shadow-lg z-[100] border max-h-32 lg:max-h-40 overflow-y-auto">
                        {timeSlots.map((time, index) => (
                          <button
                            key={index}
                            onClick={() => handleSelectTime(time)}
                            className="w-full text-left px-2.5 lg:px-3 py-1.5 lg:py-2 text-gray-700 hover:bg-gray-50 first:rounded-t-lg last:rounded-b-lg transition-colors duration-150 text-xs md:text-sm lg:text-base"
                          >
                            {time}
                          </button>
                        ))}
                        {timeSlots.length === 0 && (
                          <div className="px-3 py-2 text-xs md:text-sm text-gray-600">
                            No times available
                          </div>
                        )}
                      </div>
                    )}
                  </div>
                </div>
              </div>
            </div>

            {/* Right Column - WhatsApp Button */}
            <div className="flex items-center justify-center">
              <div className="rounded-lg p-2 lg:p-3 xl:p-4 text-center w-full max-w-sm">
                <button
                  onClick={handleWhatsAppClick}
                  className="w-full bg-[#0077b6] hover:bg-[#0077b6]/50 cursor-pointer text-[#FAEBCD] py-2 lg:py-2.5 xl:py-3 px-2.5 lg:px-3 xl:px-4 rounded-lg transition-colors duration-200 shadow-lg flex items-center justify-center font-semibold text-xs md:text-sm lg:text-base"
                >
                  <CalendarCheck className="w-3 h-3 lg:w-4 lg:h-4 xl:w-5 xl:h-5 mr-1.5" />
                  Book Now!
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default ContactComponent;